export const minutesOptions = Array(60)
  .fill({})
  .map((_, i) => ({
    value: `${i}`,
    label: `${i}`.length < 2 ? `0${i}` : `${i}`,
  }));

export const hoursOptions = Array(24)
  .fill({})
  .map((_, i) => ({
    value: `${i}`,
    label: `${i}:00`.length < 5 ? `0${i}:00` : `${i}:00`,
  }));

export const daysOfMonthOptions = Array(31)
  .fill({})
  .map((_, i) => ({
    value: `${i + 1}`,
    label: `${i + 1}`.length < 2 ? `0${i + 1}` : `${i + 1}`,
  }));

